
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'


import scroll from "../../scroll";
import { mapState, mapGetters } from "vuex";

export default {
directives: {
TransferDom
},
components: {
Group,
Cell,
Popup,
Selector,

XButton,
scroll

},
computed: {
...mapGetters([
"integPopup",
"integralQuarterList",
"integraYearlList",
"integralQuarterSelect",
"integraYearSelect",
"integralQuarterTotal",
"integraYearlTotal",
"branchList"
]),
pullDownRefreshObj: function() {
return this.pullDownRefresh
? {
threshold: parseInt(this.pullDownRefreshThreshold),
stop: parseInt(this.pullDownRefreshStop),
text: "刷新完成"
}
: false;
},
pullUpLoadObj: function() {
return this.pullUpLoad
? {
threshold: parseInt(this.pullUpLoadThreshold),
txt: {
more: this.pullUpLoadMoreTxt,
noMore: this.pullUpLoadNoMoreTxt
}
}
: false;
}
},
watch: {
integPopup: function(Value) {
this.isShow = Value;
},
integralQuarterList: function(val) {
this.currentLists = val;
},
integraYearlList: function(val) {
this.currentLists = val;
},
integralQuarterSelect: function(val) {
this.currentLists = val;
},
integraYearSelect: function(val) {
this.currentLists = val;
},
pullDownRefreshObj: {
handler(val) {
const scroll = this.$refs.scroll.scroll;
if (val) {
scroll.openPullDown();
} else {
scroll.closePullDown();
}
},
deep: true
},
pullUpLoadObj: {
handler(val) {
const scroll = this.$refs.scroll.scroll;
if (val) {
scroll.openPullUp();
} else {
scroll.closePullUp();
}
},
deep: true
},
startY() {
this.rebuildScroll();
},






















branchList: function(val) {
this.ajaxData = val;
},
$route(to, from) {
if (to.query.status === "quarter") {
if (this.integralQuarterList.length == 0) {
this.$store.dispatch("getIntegralQuarterList");
}
this.currentLists = this.integralQuarterList;
} else if (to.query.status === "year") {
if (this.integraYearlList.length == 0) {
this.$store.dispatch("getIntegralYearList");
}
this.currentLists = this.integraYearlList;
}
}
},
created() {
this.$store.dispatch("getBranchList");
if (this.$route.query.status === "quarter") {
this.$store.dispatch("getIntegralQuarterList");
} else if (this.$route.query.status === "year") {
this.$store.dispatch("getIntegralYearList");
}
},
activated() {
this.ajaxData = this.branchList;
if (this.$route.query.status === "quarter") {
this.currentLists = this.integralQuarterList;
} else if (this.$route.query.status === "year") {
this.currentLists = this.integraYearlList;
}
},
data() {
return {
pullDownRefresh: true,
pullDownRefreshThreshold: 90,
pullDownRefreshStop: 40,
pullDownRefreshText: "刷新完成",
pullUpLoad: true,
pullUpLoadThreshold: 0,
pullUpLoadMoreTxt: "加载更多",
pullUpLoadNoMoreTxt: "没有更多数据了",
startY: 0,
scrollToX: 0,
scrollToY: -200,
scrollToTime: 700,
scrollToEasing: "bounce",
scrollToEasingOptions: ["bounce", "swipe", "swipeBounce"],
quarterpage: 1,
yearpage: 1,
num: 10,



currentLists: [],
isShow: false,
ajaxData: [],
selectedData: "",
searchText: ""
};
},
methods: {
scrollTo() {
this.$refs.scroll.scrollTo(
this.scrollToX,
this.scrollToY,
this.scrollToTime,
ease[this.scrollToEasing]
);
},
onPullingDown() {
console.log("pulling down and load data");
setTimeout(() => {
if (this._isDestroyed) {
return;
}
if (this.$route.query.status === "quarter") {
this.$store.dispatch("getIntegralQuarterList");
this.quarterpage = 1;
} else if (this.$route.query.status === "year") {
this.$store.dispatch("getIntegralYearList");
this.yearpage = 1;
}
this.$refs.scroll.forceUpdate();
}, 2000);
},
onPullingUp() {
console.log("pulling up and load data");
setTimeout(() => {
if (this._isDestroyed) {
return;
}
if (this.$route.query.status === "quarter") {
this.quarterpage++;
this.$store
.dispatch("getIntegralQuarterList", { page: this.quarterpage })
.then(() => {
if (this.currentLists.length >= this.integralQuarterTotal) {
this.$refs.scroll.forceUpdate();
}
});
} else if (this.$route.query.status === "year") {
this.yearpage++;
this.$store
.dispatch("getIntegralYearList", { page: this.yearpage })
.then(() => {
if (this.currentLists.length >= this.integraYearlTotal) {
this.$refs.scroll.forceUpdate();
}
});
}
}, 1500);
},

rebuildScroll() {
Vue.nextTick(() => {
this.$refs.scroll.destroy();
this.$refs.scroll.initScroll();
});
},


















































hidePopup() {
this.$store.dispatch("updateIntegPopup", {
integPopup: false
});
},
searchData() {
this.searchText = searchText;
},
choosedData() {
this.$store.dispatch("updateIntegPopup", {
integPopup: false
});
if (this.$route.query.status === "quarter") {
this.$store.dispatch("getIntegralQuarterSelect", {
id: this.selectedData
});
} else if (this.$route.query.status === "year") {
this.$store.dispatch("getIntegralYearSelect", {
id: this.selectedData
});
}
this.selectedData = "";
}
}
};



